#header {
    width: 100%;
    height: 100%;
    display: -webkit-box;
    -webkit-box-align: center;
    -webkit-box-pack: center;
}
#header .prologue {
    width: 50%;
    height: 40%;
    margin-top: -8%;
    position: relative;
}
#header h1 span:nth-of-type(1) {
    font: 2.2em/1.3em 'rzrx';
    color: #272727;
}
#header h1 span:nth-of-type(2) {
    font: 1.5em 'rzrx';
    color: #272727;
}
#header p {
    font: 0.8em 'pf-m';
    color: #272727;
    position: absolute;
    left: 2px;
    bottom: 0.7em;
}
#header p em {
    color: #f3333f;
}


