section {
    width: 100%;
    overflow: hidden;
    display: -webkit-box;
    -webkit-box-pack: center;
}
section .wrap {
    width: 90%;
    background: white;
    border-radius: 6px;
}
nav {
    width: 100%;
    height: 4em;
    display: -webkit-box;
    -webkit-box-pack: center;
    position: relative;
}
nav ul {
    width: 100%;
    height: 100%;
    padding: 0 5%;
    -webkit-box-sizing: border-box;
            box-sizing: border-box;
    display: -webkit-box;
    -webkit-box-pack: center;
    -webkit-transition: 0.7s;
    -o-transition: 0.7s;
    transition: 0.7s;
    position: relative;
}
nav ul li {
    width: 30%;
    text-align: center;
    font: 1.4em/4em 'pf-r';
    color: #8b8b8b;
    cursor: pointer;
    -webkit-transition: 0.7s;
    -o-transition: 0.7s;
    transition: 0.7s;
}
nav ul li:hover {
    color: #000000;
}
nav ul .active {
    color: #000000;
    font-weight: bold;
}
section .wrap .con {
    width: 100%;
    height: 100em;
    margin-top: 2em;
    display: -webkit-box;
    overflow: hidden;
    position: relative;
}
section .wrap .con .conWrap {
    width: 100%;
    height: 100%;
    -webkit-transition: 0.7s;
    -o-transition: 0.7s;
    transition: 0.7s;
    display: -webkit-box;
}
.con .article,.con .project,.con .intro {
    width: 100%;
    -webkit-box-sizing: border-box;
            box-sizing: border-box;
    padding: 0 5%;
    background-clip: content-box;
    position: absolute;
    left: 0;
    top: 0;
}
.con .article {
    display: -webkit-box;
}
.con .article .artNav {
    width: 260px;
}
.article .artNav li {
    padding-left: 20%;
    -webkit-box-sizing: border-box;
            box-sizing: border-box;
}
.article .artNav li a{
    font: 1em/2em 'pf-m';
    color: #8b8b8b;
}
.article .artNav li .active {
    color: black;
    font: 'pf-b';
}
.con .article .artCon {
    -webkit-box-flex: 1;
}
.article .artCon li {
    padding: 5%;
}
.article .artCon li h3 {
    font: 1.5em/3em 'pf-b';
    text-align: center;
}
.article .artCon li span {
    font: 1em/2.5em 'pf-m';
}
.article .artCon li p {
    text-indent: 2em;
    font: 1em/1.5em 'pf-r';
}
.article .artCon li .time {
    text-align: right;
    font: 0.7em/2em 'pf-r';
}

.project {
    display: -webkit-box;
}
.project div {
    -webkit-box-flex: 1;
    margin: 2%;
    padding: 1%;
    background: #f7f7f7;
    border-radius: 8px;
    cursor: pointer;
}
.project div h4 {
    font: 1em/3em 'pf-b';
}
.project div p {
    font: 0.8em/1.5em 'pf-r';
    color: #272727;
}
.project img {
    width: 100%;
    height: 100%;
}
.intro {
    display: -webkit-box;
}
.intro .introWrap {
    -webkit-box-flex: 1;
    margin: 0 15%;
}
.intro .pic{
    width: 300px;
    height: 300px;
    background-clip: border-box;
    border: 5px solid #8b8b8b;
    border-radius: 50%;
    background: url("./Content/Intro/img/tx.jpg") no-repeat;
    background-size: cover;
    margin: 20px auto;
}
.intro p {
    text-align: center;
    font: 1em/2em 'pf-m';
    color: #272727;
}
.intro ul {
    margin-top: 10%;
    display: -webkit-box;
}
.intro li {
    -webkit-box-flex: 1;
    text-align: center;
}


