html{
    height: 100%;
}
body,ol,ul,h1,h2,h3,h4,h5,h6,p,th,td,dl,dd,form,fieldset,legend,input,textarea,select {
    margin:0;padding:0
}
body{
    width: 100%;
    height: 100%;
    background:#f6f6f2;
    font-size: 100%;
}
@font-face {
    font-family: 'pf-b';
    src: url("./font/PingFang-B.ttf");
}
@font-face {
    font-family: 'pf-m';
    src: url("./font/PingFang-M.ttf");
}
@font-face {
    font-family: 'pf-r';
    src: url("./font/PingFang Regular.ttf");
}
@font-face {
    font-family: 'rzrx';
    src: url("./font/rzrx.ttf");
}
a{
    text-decoration:none
}
em{
    font-style:normal
}
li{
    list-style:none
}
img{
    border:0;vertical-align:middle
}
#root {
    height: 100%;
}
.App {
    height: 100%;
}



